<template>
  <div>
    <div class="container containerp">
      <div class="header">
        <div>

          <van-cell
            title="点击选择日期区间"
            :value="date"
            @click="show = true"
          />
        </div>

        <van-calendar
          color="#40b9ec"
          :min-date="minDate"
          :max-date="maxDate"
          v-model="show"
          type="range"
          allow-same-day="true"
          @confirm="onConfirm"
        />
        <ul class="tab">
          <li>员工</li>
          <li>日期</li>
          <li>工序</li>
          <li>数量</li>
        </ul>
      </div>
      <div class="content">
        <ul>
          <router-link
            :to="{ path: '/InvoiceDetails/' + item.poid }"
            v-for="(item, index) in list"
            :key="index"
          >
            <li>
              <div>{{ item.user_login }}</div>
              <div>{{ formatDates(item.completion_time) }}</div>
              <div>{{ item.pname }}</div>
              <div>{{ item.com_num }}</div>
            </li>
          </router-link>

        </ul>
      </div>
    </div>
    <!--setting-->
    <van-action-sheet v-model="sshow" :actions="actions" @select="onSelect" />
  </div>
</template>

<script>
import { jobdayTable } from "@api/user";
import { Icon, Calendar, ActionSheet, Cell } from "vant";
import Loadings from "@components/Loadings";

export default {
  name: "Analysis",
  components: {
    [Icon.name]: Icon,
    Loadings,
    [Cell.name]: Cell,
    [ActionSheet.name]: ActionSheet,
    [Calendar.name]: Calendar,
  },
  props: {},
  data: function () {
    return {
      data: null,
      list: [],
      show: false,
      sshow: false,
      minDate: new Date(2020, 1, 1),
      actions: [
        { name: "密码修改" },
        { name: "退出登录" },
      ],
    };
  },
  created() {
    this.getdata();
  },

  mounted: function () {},
  methods: {
    formatDate(date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    },
    onSelect(e) {
       if (e.name == "密码修改") {
        this.show = false;
        this.$router.replace({ path: "/change_password" });
      } else {
        this.show = false;
        localStorage.remove("ntoken");
        this.$router.replace({ path: "/" });
      }
    },
    onConfirm(date) {
      const [start, end] = date;
      console.log(start.getFullYear());
      this.show = false;
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
      this.getdata({
        str_time: `${start.getFullYear()}-${
          start.getMonth() + 1
        }-${start.getDate()}`,
        end_time: `${end.getFullYear()}-${end.getMonth() + 1}-${end.getDate()}`,
      });
    },

    getdata(datas = {}) {
      jobdayTable(datas)
        .then(({ data }) => {
          this.list = data.list;
        })
        .catch((e) => {
          Toast(e.msg);
        });
    },
    formatDates: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script> 
<style scoped>
.setting {
  padding: 0 20px;

}
.header {
  position: fixed;
  top: 0;
  left: 0;
  background: #fff;
  right: 0;
  z-index: 66;
}
.tab {
  background: #eee;
  padding: 15px 0;
  overflow: hidden;
}
.tab li {
  width: 25%;
  text-align: center;
  float: left;
}
.content {
  padding-top: 100px;
}
.content {
  background: #fff;
}
.content li {
  overflow: hidden;
  line-height: 40px;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}
.content li div {
  width: 25%;
  text-align: center;
  float: left;
}
</style>
